export default `
<script type="text/javascript" src="/static/js/demo_share.js"></script>
<script>
  wdb.setTitle('js对象的get和set')
</script>

<script>
  //存取器属性和数据属性
  {
    let num = {
      a: 2,
      get b(){
        return 5
      }
    }
    wdb.write(num.a, num.b)
  }
</script>
<hr>

<script>
  //es5的原型属性的写法
  {
    function Num (n) {
      this._num = n
    }
    Num.prototype = {
      get num () {
        wdb.write('get num')
        return this._num
      },
      set num (value) {
        wdb.write('set num', value)
        this._num = value
      }
    }
    let n = new Num(5)
    n.num
    n.num = 15
  }
</script>
<hr>

<script>
  //es5的对象属性写法
  {
    function Num (n) {
      let me = this
      this._num = n
      return {
        get num () {
          wdb.write('get num')
          return me._num
        },
        set num (value) {
          wdb.write('set num', value)
          me._num = value
        }
      }
    }
    let n = new Num(2)
    n.num
    n.num = 4
  }
</script>
<hr>

<script>
  //es6写法
  {
    class Num {
      constructor (n) {
        this._num = n
      }
      get num () {
        wdb.write('get num')
        return this._num
      }
      set num(value) {
        wdb.write('set num', value)
        this._num = value
      }
    }
    let n = new Num(3)
    n.num
    n.num = 6
  }
</script>
`



